<template>
  <div>
    <p class="vertical"></p>
    <h1 id="title-effect">你好 我是趙生凱</h1>
    <p class="content-effect">
      2018年6月進入網頁前端領域 <br />擅長CSS&JS製作html、SVG動畫效果
      <router-link to="/about">About Me...</router-link>
    </p>
  </div>
</template>
<script>
var is = require("@/js/valid");
export default {
  data() {
    return {
      t: "",
      p: ""
    };
  },
  mounted() {
    (this.t = document.querySelector("#title-effect")),
      (this.p = document.querySelector(".content-effect"));
    if (is.PC() && this.t && this.p) {
      setTimeout(() => {
        document.addEventListener("mousemove", this.parallox);
      }, 600);
    }
  },
  methods: {
    parallox(e) {
      let w = window.outerWidth,
        h = window.outerHeight,
        mX = e.clientX,
        mY = e.clientY;
      this.t.style.transform = `translate(${-(w - mX) / 200 + 20}px ,${-(
        h - mY
      ) / 200}px)`;
      this.p.style.transform = `translate(${-(w - mX) / 150 + 20}px ,${-(
        h - mY
      ) / 150}px)`;
    }
  },
  beforeDestroy() {
    this.t = this.p = "";
    document.removeEventListener("mousemove", this.parallox);
  }
};
</script>

<style lang="scss" scoped>
@import url("https://fonts.googleapis.com/css?family=Noto+Sans+TC");
h1 {
  font-size: 60px;
  font-family: "Noto Sans TC", sans-serif;
  color: #198cd1;
}
p {
  font-size: 30px;
  letter-spacing: 2px;
}
a {
  display: inline-block;
  margin-top: 10px;
  padding-right: 20px;
  color: #eee;
  position: relative;
  transition: 0.3s;
  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    height: 2px;
    transition: 0.3s;
    background-color: #198cd1;
  }
  &:hover {
    color: #198cd1;
    &::before {
      width: 100%;
      background-color: #198cd1;
    }
  }
}
@media (max-width: 575.98px) {
  h1 {
    text-align: center;
    font-size: 30px;
  }
  p {
    text-align: center;
    font-size: 16px;
  }
}
</style>
