      skillShow: false
    };
  },
  mounted() {
    window.addEventListener("scroll", this.showSkills);
  },
  methods: {
    showSkills() {
      var sec = document.getElementsByTagName("section");
      var h = sec[0].scrollHeight + sec[1].scrollHeight;
